      <template scope="scope">
      <el-image
      style="width: 50px; height: 50px"
      :src="scope.row.image"
      :fit="fit"></el-image>
      </template>
    </el-table-column>

     <el-table-column
      prop="title"
      label="姓名和年龄"
      align="center"
      width="280">
     </el-table-column>

       <el-table-column
      prop="category"
      label="性别"
      align="center"
      width="280">
       </el-table-column>

      <el-table-column
      prop="poston"
      align="center"
      width="280"
      label="发布时间">
     </el-table-column>

      <el-table-column
      align="center"
      label="已有线索数">
      <template slot-scope="scope">{{ scope.row.clueInfos.length }}</template>
    </el-table-column>

       <el-table-column
      fixed="right"
      label=""
      width="100">
      <template slot-scope="scope">
        <el-button @click="viewClues(scope.row)" type="text" size="small" >查看线索</el-button>
      </template>
    </el-table-column>


    </el-table>
    <div style="width:100%">
      <el-button type="primary" @click="dialogVisible = true" class="addperson">发布寻亲信息</el-button>
    </div>
    <el-dialog
  title="发布寻亲信息"
  :visible.sync="dialogVisible"
  width="40%">
  <!--<span slot="footer" class="dialog-footer">-->
  <el-form ref="form"   label-width="100px" >
     <el-form-item label="姓名和年龄">
         <el-input placeholder="姓名和年龄" v-model="ruleForm.title" clearable></el-input>
      </el-form-item>
    
     <el-form-item label="性别">
          <el-radio-group v-model="ruleForm.category">
            <el-radio label="男">男</el-radio>
            <el-radio label="女">女</el-radio>
          </el-radio-group>
      </el-form-item>
     <el-form-item label="描述">
         <el-input type="textarea" :rows="2" placeholder="请描述走失人员的出生年月，走失时间，地点等信息" v-model="ruleForm.detail" clearable></el-input>
    </el-form-item>
    <el-form-item label="图片">
          <images-upload :tips="tips" :moreUpload="false" :index="1" :onSuccess="uploadSuccess"></images-upload>
    </el-form-item>

    <el-form-item>
        <el-button @click="dialogVisible = false">返回</el-button>
        <el-button type="primary" @click="onSubmit(true,1)">确定</el-button>
    </el-form-item>

  </el-form>
</el-dialog>
 <el-dialog
  title="线索"
  :visible.sync="threadVisible"
  width="80%">
  <el-table :data="threadTable"
    stripe
    :default-sort = "{prop: 'status',}"
    style="width: 100%">

    <el-table-column
      label=""
      width="40">
    </el-table-column>

      <el-table-column type="expand">
      <template slot-scope="props">
        <div style="text-align:center">
