     </el-table-column>

     <el-table-column
      prop="confidence"
      label="相似度"
      width="140"
      align="center">
     </el-table-column>

      <el-table-column label="是否匹配" align="center" width="140">
        <template slot-scope="scope">
           <p v-if="scope.row.status==0">未确认</p>
           <p v-if="scope.row.status==1">是</p>
           <p v-if="scope.row.status==2">不是</p>
        </template>
      </el-table-column>

      <el-table-column label="" align="center" >
        <template slot-scope="scope">
           <el-button type="primary" @click="confirmyes(scope.row,scope.$index)"  v-if="scope.row.status==0">是</el-button>
           <el-button type="primary" @click="confirmyno(scope.row,scope.$index)"  v-if="scope.row.status==0">不是</el-button>
        </template>
      </el-table-column>

    

    </el-table>
  </el-dialog>
</div>
</template>


<script>
  export default {
    data() {
      return {
        tableData: [{
          date: '2016-05-04',
          name: '王小虎',
          clueInfos: [],
          url: 'https://kjcxtest.crc.com.cn/group1/M00/00/01/CgBj1FzU5QqAGD9qAAGJHIGlqnk829.png'
        }],

        threadTable:[],

        ruleForm: {
          title: "",
          category: "男",
          detail: "",
          imgUrl: ""
        },
        page: {
          pageIndex: '1',
          pageSize:  '10'
        },
      
        dialogVisible: false,
        threadVisible: false,
        imageUrl: '',
        sex: '1',
        fit: '',
        tips: ''
      
      }
    },
     created() {
        this.getDataList();
    },
    activated() {
      this.getDataList();
    },
    methods: {
      async getDataList() {
        const data = await this.$api.publishList(this.page);
        this.tableData = data;
      },
      async onSubmit(){
        const result = await this.$api.addPublish(this.ruleForm);
        if(!result){
          return false;
        }
        this.getDataList()
        this.dialogVisible = false
        this.clearForm();
      },
      uploadSuccess(imgUrl) {
          
         this.ruleForm.imgUrl = imgUrl;    
      },
      clearForm(){
        this.ruleForm.title = '';
        this.ruleForm.category = '男';
        this.ruleForm.detail = '';
        this.ruleForm.imgUrl = '';
      },
      viewClues(row){

        this.threadTable = row.clueInfos
        this.threadVisible = true
      },
      async confirmyes(row,index){
        const data = {
          id: row.id,
          status: 1
        }
        const result = await this.$api.confirmClueInfo(data);
        if(!result){
          return false;
        }
        this.threadTable[index].status = 1
      },
      async confirmyno(row,index){
        const data = {
          id: row.id,
          status: 2
        }
        const result = await this.$api.confirmClueInfo(data);
        if(!result){
          return false;
        }
        this.threadTable[index].status = 2
      }
    }
  }
</script>

<style>
.addperson {
  
  float: right;
